import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ViewPageResolver } from '@presentation/reservations/resolvers/view.page.resolver';
import { ListComponent } from './components/list/list.component';
import { ViewComponent } from './components/view/view.component';
import { TitleResolver } from '@presentation/reservations/resolvers/title.resolver';

const routes: Routes = [
  {
    path: ':type',
    children: [
      {
        path: '',
        pathMatch: 'full',
        component: ListComponent,
        data: {
          title: ' ',
          componentName: 'ReservationsListComponent',
        },
        resolve: {
          title: TitleResolver,
        },
      },
      {
        path: 'create',
        loadChildren: () =>
          import('./reservation-create/reservation-create.module').then(
            ({ ReservationCreateModule }) => ReservationCreateModule
          ),
      },
      {
        path: 'workplace',
        loadChildren: () =>
          import(
            './reservation-workplace-create/reservation-workplace-create.module'
          ).then(
            ({ ReservationWorkplaceCreateModule }) =>
              ReservationWorkplaceCreateModule
          ),
      },
      {
        path: 'view/:id',
        component: ViewComponent,
        data: {
          showBackBtn: true,
        },
        resolve: {
          title: ViewPageResolver,
        },
      },
    ],
  },
  {
    path: '',
    pathMatch: 'full',
    redirectTo: '/reservations/workplace',
  },
  {
    path: '**',
    redirectTo: '/reservations/workplace',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ReservationsRoutingModule {}
